<template>
  <div class="container">
    <!-- 标题 -->
    <page-title first-title="运费模板"></page-title>
    <!-- 按钮 -->
    <div class="btn-add-shipping">
      <el-button type="primary" @click="addShipping()">添加运费模板</el-button>
    </div>
    <!-- 模板列表 -->
    <div class="shipping-templates-list">
      <el-table
        :data="shippingLists"
        stripe
        style="width: 100%"
        v-loading="loading"
        :header-cell-style="{ background: '#eef1f6' }"
      >
        <el-table-column prop="name" label="模板名称"></el-table-column>
        <el-table-column prop="created_at" label="创建时间" width="200"></el-table-column>
        <el-table-column prop="updated_at" label="更新时间" width="200"></el-table-column>
        <el-table-column label="操作" width="100">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="editShipping(scope.row.id, scope.row.name, scope.row.tpl_type)"
              >编辑</el-button
            >
            <el-button type="text" size="small" @click="deleteShipping(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
import { getShippingTplList, delShippingTpl } from '../../api/shippingtpl'
export default {
  name: 'SuppliersShippingTemplates',
  data() {
    return {
      shippingLists: [],
      loading: true
    }
  },
  created() {
    // 初始化模板列表
    this.getList()
  },
  methods: {
    // 获取列表
    getList() {
      getShippingTplList().then(res => {
        this.shippingLists = res
        this.loading = false
      })
    },
    // 添加运费模板
    addShipping() {
      this.$router.push({ name: 'SuppliersShippingTemplatesAdd' })
    },
    // 编辑运费模板
    editShipping(id, name, type) {
      this.$router.push({ name: 'SuppliersShippingTemplatesAdd', query: { tplID: id, tplName: name, type: type } })
    },
    // 删除运费模板
    deleteShipping(id) {
      delShippingTpl(id).then(res => {
        // 在前端删掉，不在调用接口
        if (res.goods_id) {
          this.$notify.warning('该运费模版正在被使用, 使用该模板的商品ID为' + res.goods_id)
        } else {
          let _list = this.shippingLists,
            len = this.shippingLists.length
          for (let i = 0; i < len; i++) {
            if (_list[i].id === id) {
              _list.splice(i, 1)
              this.$notify.success('删除成功')
              break // 删除后跳出循环，不再执行，否则报错
            }
          }
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.btn-add-shipping {
  .el-button {
    float: right;
    margin: 15px 0;
  }
}
</style>
